<template>
  <div>
    <button @click="listMessages">Cargar mensajes</button>
    <div v-for="message in messages" :key="message.id">
      <h2>{{ message.subject }}</h2>
      <p>{{ message.snippet }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      messages: []
    }
  },
  methods: {
    listMessages() {
      gapi.client.gmail.users.messages.list({
        'userId': 'me'
      }).then(response => {
        const messages = response.result.messages;
        if (messages && messages.length > 0) {
          this.messages = messages.map(message => {
            // Aquí deberías obtener los detalles del mensaje usando el ID
            // Este es solo un ejemplo, necesitarás adaptarlo a tus necesidades
            return {
              id: message.id,
              subject: 'Asunto del mensaje',
              snippet: 'Fragmento del mensaje'
            }
          });
        } else {
          console.log('No new messages.');
        }
      });
    }
  }
}
</script>